.App-Burger {
  position: absolute;
  top: 0;
  right: 0;
  opacity: 1;
  -webkit-transition: 400ms;
  -o-transition: 400ms;
  transition: 400ms; }
  .App-Burger.true {
    opacity: 0; }

.view-wrapper {
  padding: 50px 0 0 0;
  -webkit-transition: -webkit-filter 300ms;
  transition: -webkit-filter 300ms;
  -o-transition: filter 300ms;
  transition: filter 300ms;
  transition: filter 300ms, -webkit-filter 300ms;
  height: 100vh;
  overflow-y: scroll; }
  .view-wrapper.true {
    -webkit-filter: blur(15px);
            filter: blur(15px); }
  @media (min-width: 992px) {
    .view-wrapper {
      padding: 0 300px 0 0; }
      .view-wrapper.true {
        -webkit-filter: blur(0);
                filter: blur(0); } }



